@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&family=Oswald:wght@200;300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  height: auto;
  width: auto;
}

.App-logo {
  display: flex;
  height: 100px;
  pointer-events: none;
  left: 0;
}

.body {
  padding-top: 5rem;
}

.App-header {
  background-color: #FFFFFF;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.IntroContainer {
    width: 350px;
    margin-left: 8rem;
}

.IntroContainer-content {
    text-align: left;
    padding: .5rem;
    color: #ffffff;
}

.IntroContainer-content--intro {
    font-family: Fira Code, sans-serif;
    font-weight: 400;
    letter-spacing: -1px;
    font-size: 16px;
    text-align: left;
    width: 200px;
}

.IntroContainer-content--name {
    font-family: Fira Code, sans-serif;
    font-weight: 600;
    font-size: 125px;
    letter-spacing: -10px;
    text-align: left;
    margin-top: -30px;
    margin-bottom: -20px;
    margin-left: -8px;
}

.IntroContainer-content--occupation {
    font-family: Fira Code, sans-serif;
    font-weight: 500;
    letter-spacing: -1px;
    font-size: 26px;
    text-align: left;
}




/*  src/Title.css  */
.Title-background {
    background: black;
    box-sizing: border-box;
    width: 100%;
    justify-content: left;
    align-items: baseline;
    padding: 1rem;
    margin-bottom: 1rem;
    color: #fff;
    min-height: 5vmin;
}

.Title-header {
    width: 30px;
}
